.App {

}

.biscuits {
    position: relative;
    margin-bottom: 10px;
}

.biscuits .components img {
    position: absolute;
    -webkit-transition: width 20s, height 20s, left 20s, top 20s, -webkit-transform 20s;
    transition: width 20s, height 20s, left 20s, top 20s, -webkit-transform 20s;
    -o-transition: width 20s, height 20s, left 20s, top 20s, transform 20s;
    transition: width 20s, height 20s, left 20s, top 20s, transform 20s;
    transition: width 20s, height 20s, left 20s, top 20s, transform 20s, -webkit-transform 20s;
    -webkit-transition-timing-function: ease-in-out;
         -o-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}




